import React from "react";
import fb from '../images/round-fb.svg';
import li from '../images/round-in.svg';
import ig from '../images/round-ig.svg';


const AboutCompo = (props) =>{
     function rotation(x){
          document.getElementById('about-x-'+x).classList.toggle("about-bars-rotation");
          document.getElementById('about-social-list-'+x).classList.toggle("about-social-list-toggle")
     }
     return(
          <div className="aboutcompo">
               <img src={props.img} alt="png"></img>
               <div className="about-social">
                    <div className="about-social-list" id={'about-social-list-'+props.id}>
                         <a href={props.inUrl}>
                              <img src={li} alt="facebook"></img> 
                         </a>
                         <a href={props.igUrl}>
                              <img src={ig} alt="facebook"></img>
                         </a>
                         <a href={props.fbUrl}>
                              <img src={fb} alt="facebook"></img> 
                         </a>
                    </div>
                    <div className="about-x">
                         <div className="about-bars"  id={'about-x-'+props.id}  onClick={() => rotation(props.id)}>
                              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
                                   <path fillRule="evenodd" d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z" clipRule="evenodd" />
                              </svg>
                         </div>
                    </div>
               </div>
               <h1 className="about-compo-name ">{props.name}</h1>
               <h2 className="about-compo-pos ">{props.position}</h2>
          </div>
     )
}


export default AboutCompo;